.pum-form-wrapper .loader {
  animation-duration: 0.75s;
  animation-iteration-count: infinite;
  animation-name: rotate-forever;
  animation-timing-function: linear;
  height: .75em;
  width: .75em;
  border: 0.25em solid rgba(0, 0, 0, 0.5);
  border-right-color: transparent;
  border-radius: 50%;
  display: inline-block;
}

.pum-form-wrapper .messages {
  display: none;
  border: 1px solid rgba(0, 0, 0, 0.25);
  margin-bottom: .5em;
  padding: 1em;
  position: relative;
}

.pum-form-wrapper .messages .message {
  margin-bottom: .5em;
}

.pum-form-wrapper .messages .message:last-child {
  margin-bottom: 0;
}

.pum-form-wrapper .messages .message.error {
  color: red !important;
  border-color: red;
}

.pum-form-wrapper .messages .message.success {
  color: green !important;
  border-color: green;
}

.pum-form-wrapper .messages .loader-wrap {
  min-height: 1em;
  text-align: center;
  margin-bottom: 0;
}

.pum-form-wrapper .messages .loader {
  font-size: 2em;
}

.pum-form-wrapper button[type="submit"] {
  position: relative;
}

.pum-form-wrapper button[type="submit"] .loader {
  margin-left: .5em;
  border: 0.25em solid rgba(255, 255, 255, 0.5);
  border-right-color: transparent;
}

.pum-alm-footer-links {
  clear: both;
  margin: 0;
  padding: .5em 0 .5em 1em;
  border-top: 1px solid #000;
}

